document.addEventListener("DOMContentLoaded", function () {
	const tabsBlocks = document.querySelectorAll(".block-tabs");
	tabsBlocks.forEach((tabsBlock) => {
		const tabs = tabsBlock.querySelectorAll(".block-tabs__tab");
		const tabContents = tabsBlock.querySelectorAll(".block-tabs__tab-content");

		tabs.forEach((tab, i) => {
			tab.addEventListener("click", function (event) {
				event.preventDefault();
				tabs.forEach((tab) => {
					tab.classList.remove("block-tabs__tab--active");
				});
				tab.classList.add("block-tabs__tab--active");
				tabContents.forEach((tabContent) => {
					tabContent.classList.remove("block-tabs__tab-content--active");
				});
				tabContents[i].classList.add("block-tabs__tab-content--active");
			});
		});
	});
});
